import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: "/AddConsumer",
    name: "AddConsumer",
    /*route level code-splitting
    this generates a separate chunk (about.[hash].js) for this route
    which is lazy-loaded when the route is visited.*/
    component: () => import("../views/AddConsumer")
  },
  {
    path: '/AddFile',
    name: "AddFile",
    component: ()=> import("../views/AddFile")
  },
  {
    path: "/PrivateArea",
    name: "PrivateArea",
    component: () => import("../views/PrivateArea")
  },
  {
    path:"/MyConsumers",
    name:"MyConsumers",
    component: () => import("../views/MyConsumers")
  },
  {
    path:"/ManageFiles",
    name:"ManageFiles",
    component: ()=> import ("../views/ManageFiles")
  }
]

const router = new VueRouter({
  routes
})

export default router
