<template>
  <div>
    <h1>Welcome, {{uploaderInfo.username}}!</h1>
    <p>Sono elencate di seguito le tue informazioni personali</p>

    <table>
      <tr>
        <th>Role</th>
        <th>Uploaded Files</th>
        <th>Affiliate Consumers</th>
      </tr>
      <tr>
        <td>{{uploaderInfo.role}}</td>
        <td>{{uploaderInfo.total_files}}</td>
        <td>{{uploaderInfo.total_consumers}}</td>
      </tr>
    </table>

    <p>Files consumers relationships</p>
    <section id="first_section">
      <ol>
        <li
          v-for="counter in Object.keys(consumers).length"
          v-bind:key="counter"
          @click="changeActiveConsumer(Object.keys(consumers).sort()[counter-1])"
        >
          <span>{{Object.keys(consumers).sort()[counter-1]}}</span>
          ({{consumers[Object.keys(consumers).sort()[counter-1]].length}})
        </li>
      </ol>
      <article v-if="consumers[activeConsumer]!==undefined && consumers[activeConsumer].length===0">
        <p>You have not uploaded any file to this user</p>
      </article>
      <table v-else>
        <colgroup>
          <col />
          <col />
          <col />
        </colgroup>
        <th>Nome File</th>
        <th>Seen Date</th>
        <th>IP Address</th>
        <tr v-for="uploadedFile in consumers[activeConsumer]" v-bind:key="uploadedFile.id">
          <td>{{uploadedFile.name}}</td>
          <td v-if="uploadedFile.seen!==''">{{uploadedFile.seen}}</td>
          <td v-else>
            <span>file has not been seen yet</span>
          </td>
          <td v-if="uploadedFile.seen!==''">{{uploadedFile.ipAddress}}</td>
          <td v-else>-</td>
        </tr>
      </table>
    </section>

  </div>
</template>

<script scoped>
// @ is an alias to /src

import Axios from "axios";

export default {
  name: "Home",
  components: {},

  methods: {
    changeActiveConsumer: function (username) {
      document.querySelectorAll("li span").forEach((spanItem) => {
        if (spanItem.innerText === this.activeConsumer) {
          spanItem.style.textDecoration = "none";
          document.querySelector("li span").style.textDecoration = "none";
          console.log(spanItem);
          console.log(spanItem.innerText);
        }
      });

      event.target.parentElement.querySelector("span").style.textDecoration =
        "underline";

      this.activeConsumer = username;
    },
  },

  data: function () {
    return {
      uploaderInfo: {},
      consumers: [],
      activeConsumer: "",
    };
  },

  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/info`)
      .then((response) => (this.uploaderInfo = response.data))
      .catch((error) => console.log(error));

    Axios.get(
      `${process.env.VUE_APP_APIROOT}uploaderarea/working`
    )
      .then((response) => {
        this.consumers = response.data;
        this.activeConsumer = Object.keys(this.consumers).sort()[0];
      })
      .catch((error) => console.log(error));
  },
};
</script>

